
form {
  transition: 0.4s opacity ease;
}
.covers {
  display: -ms-grid;
  display: grid;
  grid-row-gap: 30px;
  grid-column-gap: 30px;
}
.cover-img:after {
  content: 'Correct!';
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: -webkit-transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: rgba(88, 158, 83, 0.8);
  color: #fff;
  z-index: 5;
}
.is-correct .cover-img:after {
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}
.is-correct input[type=text] {
  pointer-events: none;
}
.is-correct form {
  opacity: 0.3;
}
.is-correct .btn--check {
  pointer-events: none;
}
.film-title {
  background: #000;
  color: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 0.5rem 1rem;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  text-align: center;
  transition: -webkit-transform 0.4s 0.5s;
  transition: transform 0.4s 0.5s;
  transition: transform 0.4s 0.5s, -webkit-transform 0.4s 0.5s;
  z-index: 10;
  width: 100%;
}
.is-correct .film-title {
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}
.is-wrong input[type='text'] {
  border-color: red;
  -webkit-animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
          animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  outline: none;
}
@media (min-width: 720px) {
.covers {
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
}
}
@media (min-width: 1100px) {
.covers {
    -ms-grid-columns: (1fr)[3];
        grid-template-columns: repeat(3, 1fr);
}
}
.cover-img {
  margin-bottom: 5px;
  position: relative;
  overflow: hidden;
}
.list--unstyled {
  list-style: none;
  padding: 0;
}
@-webkit-keyframes shake {
10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
}
20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
}
30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
}
40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
}
}
@keyframes shake {
10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
}
20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
}
30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
}
40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
}
}



